/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  overflow-x: hidden;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8fafc;
}

.App {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

/* Header Section */
.header-section {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 40px 0;
  margin: 0 0 40px 0; /* Removed negative margins */
  text-align: center;
}

.header-section h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.header-section p {
  font-size: 1.1rem;
  opacity: 0.9;
}

/* Filter Controls */
.filter-controls {
  background: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 12px;
}

.filter-controls label {
  font-weight: 600;
  color: #374151;
  font-size: 0.95rem;
}

.filter-controls select {
  padding: 12px 16px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  background: white;
  font-size: 0.95rem;
  color: #374151;
  transition: all 0.2s ease;
  min-width: 180px;
}

.filter-controls select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.cart-button {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.cart-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(245, 87, 108, 0.3);
}

.cart-button:active {
  transform: translateY(0);
}

/* Products Grid */
.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
  margin-top: 30px;
}

/* Product Cards */
.product {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  border: 1px solid #f1f5f9;
  position: relative;
}

.product:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border-color: #e2e8f0;
}

.product-image-container {
  position: relative;
  height: 240px;
  overflow: hidden;
  background: #f8fafc;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.product img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.product:hover img {
  transform: scale(1.05);
}

.product-content {
  padding: 24px;
}

.product-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 8px;
  cursor: pointer;
  transition: color 0.2s ease;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 1.4;
  min-height: 2.8em;
}

.product-title:hover {
  color: #667eea;
}

.product-category {
  font-size: 0.85rem;
  color: #6b7280;
  background: #f3f4f6;
  padding: 4px 12px;
  border-radius: 20px;
  display: inline-block;
  margin-bottom: 16px;
  font-weight: 500;
  text-transform: capitalize;
}

.product-price {
  font-size: 1.4rem;
  font-weight: 700;
  color: #059669;
  margin-bottom: 16px;
}

.add-to-cart-btn {
  width: 100%;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.add-to-cart-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(16, 185, 129, 0.3);
}

.add-to-cart-btn:active {
  transform: translateY(0);
}

/* Loading and Empty States */
.loading-message, .no-products-message {
  text-align: center;
  padding: 60px 20px;
  color: #6b7280;
  font-size: 1.1rem;
}

.loading-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 3px solid #e5e7eb;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.no-products-message {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  margin: 40px 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .App {
    padding: 15px;
  }
  
  .header-section {
    margin: 0 0 30px 0; /* Removed negative margins */
    padding: 30px 15px;
  }
  
  .header-section h1 {
    font-size: 2rem;
  }
  
  .filter-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
  }
  
  .filter-group {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  
  .filter-controls select {
    min-width: auto;
  }
  
  .products {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .product-content {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .products {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .product-image-container {
    height: 200px;
    padding: 15px;
  }
  
  .product-content {
    padding: 16px;
  }
}

/* Additional Professional Touches */
.product::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.product:hover::before {
  opacity: 1;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Focus styles for accessibility */
button:focus, select:focus {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}
/* Product Details Page - Unique Classes */
.product-details-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: #f8fafc;
}

.product-page-title {
  font-size: 2rem;
  font-weight: 700;
  color: #1f2937;
  text-align: center;
  margin-bottom: 40px;
}

.product-main-content {
  display: flex;
  gap: 60px;
  background: white;
  padding: 50px;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  align-items: flex-start;
}

.product-detail-image {
  width: 400px;
  height: 400px;
  object-fit: contain;
  background: #f8fafc;
  border-radius: 12px;
  padding: 20px;
  flex-shrink: 0;
}

.product-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.product-category-tag {
  font-size: 1rem;
  color: #667eea;
  font-weight: 600;
  text-transform: capitalize;
  background: #f0f4ff;
  padding: 8px 16px;
  border-radius: 20px;
  width: fit-content;
}

.product-desc {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #4b5563;
}

.product-detail-price {
  font-size: 2rem;
  font-weight: 800;
  color: #059669;
}

.product-cart-btn {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: fit-content;
}

.product-cart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(16, 185, 129, 0.3);
}

.product-loading {
  text-align: center;
  padding: 100px 20px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  color: #6b7280;
  font-size: 1.2rem;
}

/* Responsive */
@media (max-width: 768px) {
  .product-main-content {
    flex-direction: column;
    gap: 30px;
    padding: 30px;
  }
  
  .product-detail-image {
    width: 100%;
    height: 300px;
    align-self: center;
  }
  
  .product-page-title {
    font-size: 1.5rem;
  }
  
  .product-detail-price {
    font-size: 1.5rem;
  }
}